.question-list
{
  padding: 0 20px;
  margin-top: 20px;
  text-align: center;
  opacity: 0;
}
.question-list.right
{
  -webkit-animation: fadeInRight ease .6s forwards;
          animation: fadeInRight ease .6s forwards;
}
.question-list.left
{
  -webkit-animation: fadeInLeft ease .6s forwards;
          animation: fadeInLeft ease .6s forwards;
}

@-webkit-keyframes fadeInRight
{
  0%
  {
    opacity: 0;
    -webkit-transform: translateX(100px);
            transform: translateX(100px);
  }
  100%
  {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

@keyframes fadeInRight
{
  0%
  {
    opacity: 0;
    -webkit-transform: translateX(100px);
            transform: translateX(100px);
  }
  100%
  {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

@-webkit-keyframes fadeInLeft
{
  0%
  {
    opacity: 0;
    -webkit-transform: translateX(-100px);
            transform: translateX(-100px);
  }
  100%
  {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

@keyframes fadeInLeft
{
  0%
  {
    opacity: 0;
    -webkit-transform: translateX(-100px);
            transform: translateX(-100px);
  }
  100%
  {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}



