.controls
{
  width: 100%;
  padding: 20px;
  position: absolute;
  display: -ms-flex;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  position: absolute;
  bottom: 0;
}
.controls__item
{
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: contain;
  margin: 0 10px;
  -webkit-transition: ease .3s;
  -o-transition: ease .3s;
  transition: ease .3s;
  cursor: pointer;
}
.controls__item:active
{
  -webkit-transform: scale(1.2);
      -ms-transform: scale(1.2);
          transform: scale(1.2);
}
.controls__item--no
{
  width: 43px;
  height: 43px;
  background-image: url('../images/no.png');
}
.controls__item--yes
{
  background-image: url('../images/yes.png');
}
@media screen and (max-width: 767px)
{
  .controls__item
  {
    width: 80px;
    height: 80px;
  }
  .controls__item--no
  {
    width: 68px;
    height: 68px;
  }
}



